body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(white,rgb(241, 171, 39), white);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.nav-image{
    height: 100px;
    width: 100px;
}
.text{
    justify-content: center;
    align-items: center;
}

.search {
    outline: none;
    width: 220px;
    height: 50px;
    border-radius: 20px;
    border: none;
    margin-bottom: 1%;
    background: rgba(250, 250, 250, 0.85);
}
.content{
    margin-bottom: 0px;
    justify-content: center;
    align-items: center;
}
.search-btn{
    border-radius: 5rem;
    color: black;
    width: 50px;
    height: 50px;
    background-color:rgba(250, 250, 250, 0.85);
    margin-left: 10px;
    border:none;
}
.search-btn:hover{
    color:rgb(255, 255, 255);
    background-color: rgb(1, 1, 1);
    
}
.card{
    border: none;
}
